import axios from "axios";

export const fetchData = (url1, url2) => {
  return async (dispatch) => {
    try {
      const response1 = await axios.get(`${url1}`);
      const response2 = await axios.get(url2);
      dispatch({
        type: "FETCH_DATA",
        payload: {
          response1: response1.data,
          response2: response2.data,
        },
      });
    } catch (err) {
      console.log("Error message : ", err.message);
    }
  };
};

export const fillCryptoTable = (url, cryptoSymbol, tableNumber) => {
  return async (dispatch) => {
    try {
      const response = await axios.get(`${url}?symbol=${cryptoSymbol}`);
      dispatch({
        type: "UPDATE_CRYPTO_PRICE",
        payload: {
          data: response.data,
          tableNo: tableNumber,
        },
      });
    } catch (err) {
      console.log("Error message", err.message);
    }
  };
};

export const updateAllData = (url1, url2, cryptoList) => {
  return async (dispatch) => {
    try {
	  //   let tab1Response = [];
	  const response1 = await axios.get(url1)
	  const response2 = await axios.get(url2)
	  const table1Symbols = cryptoList.table1Data.map(crypto=>crypto.symbol)
	  const table2Symbols = cryptoList.table2Data.map(crypto=>crypto.symbol)
	  const tab1Response = response1.data.filter(crypto=>table1Symbols.includes(crypto.symbol))
	  const tab2Response = response2.data.filter(crypto=>table2Symbols.includes(crypto.symbol))
      dispatch({
        type: "UPDATE_DATA",
        payload: {
          tab1Response : tab1Response,
          tab2Response : tab2Response,
        },
      });
    } catch (err) {
      console.log("Error message", err.message);
    }
  };
};
